a:hover{text-decoration:none;color:#008ae6}a:active,a:focus{color:#008ae6}a::before{content:'';display:block;position:absolute;top:80%;height:3px;width:100%;background-color:#008ae6;-webkit-transform-origin:center top;transform-origin:center top;-webkit-transform:scale(0,1);transform:scale(0,1);transition:color .1s,-webkit-transform .2s ease-out;transition:color .1s,transform .2s ease-out;transition:color .1s,transform .2s ease-out,-webkit-transform .2s ease-out}a:active::before{background-color:#008ae6}a:focus::before,a:hover::before{-webkit-transform-origin:center top;transform-origin:center top;-webkit-transform:scale(1,1);transform:scale(1,1)}.container-fluid{line-height:24px}header{position:relative;display:block;width:100%;color:#fff;height:90px;font-weight:700}header .logo{position:absolute;top:50%;left:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}header .logo img{height:50px}header nav,header nav ul,header nav ul li{margin:0;padding:0;margin-left:25px}header nav{position:absolute;margin-top:1px;top:50%;right:15px;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}header nav ul li{list-style-type:none;display:inline-block;margin-right:10px}header nav ul li.last{margin-top:0;margin-left:25px}header nav ul li a{font-size:16px;line-height:normal;color:#010000}footer{position:absolute;bottom:0;display:block;width:100%;color:#010000;height:90px}footer .footerText{position:absolute;top:85%;left:45%;font-size:12px;text-align:center;-webkit-transform:translateY(-50%);-moz-transform:translateY(-50%);transform:translateY(-50%)}